.quickSearchContainer{
    margin-left:115px;
    margin-top:50px;;
    float: left;
}

.quickSearchHeading{
    color:#192f60;
    font-size:30px;
    font-weight:bold;
    font-family:Poppins;
    line-height: .1;
}

.quickSearchSubHeading{
    color:gray;
