    font-weight: normal;
    font-family: Poppins;
}

.tilecontainer{
    width:350px;
    height:150px;
    background-color:#ffffff;
    box-shadow: 6px 10px 15px black;
    float:left;
    margin-right:30px;
    margin-bottom:30px;
}

.tileComponent1{
    width:50%;
    float: left;
}

.tileComponent2{
    width:50%;
    float: left;
}

