}

.componentSubHeading{
    color:gray;
    font-size:18px;
}

@media only screen and (max-width:500px) {
    .tilecontainer{
        width:330px;
        height:150px;
        background-color:#ffffff;
        box-shadow: 6px 10px 15px black;
        float:left;
        margin-bottom:10px;
