footer{
    padding: 150px 0 20px;
}

hr{
    width: 100%;
    border: 0;
    border-top: 1px solid #f67c92;
}

.footer-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footer-left-col{
    flex-basis: 50%;
    margin-top: 10px;
    
}

.footer-right-col{
    flex-basis: 35%;
    
}

.footer-links{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.link-title h4{
    color: #fab3c4;
    margin-bottom: 10px;
}

.link-title small{
    font-size: 15px;
    color: #777;
}

.footer-info{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footer-logo img{
    width: 100px;
}

.footer-logo button{
    padding: 5px 38px;
    margin-top: 5px;
    font-size: 10px;
}

.copyright-text,.footer-logo{
    flex-basis: 40%;
    
}